import request from '@/utils/request'

// 获取商品
export const getProduct = (obj) => {
  const { categoryId, goodsName, page } = obj
  return request({
    url: '/goods/list',
    params: {
      categoryId,
      goodsName,
      page
    }
  })
}

// 获取商品详细页
export const getProDetail = (goodsId) => {
  return request({
    url: '/goods/detail',
    params: {
      goodsId
    }
  })
}

// 获取商品评价
export const getListRows = (goodsId) => {
  return request({
    url: '/comment/listRows',
    params: {
      goodsId,
      limit: 3
    }
  })
}
